import {mapGetters, mapState} from 'vuex';
import appCashModel from "../../../components/page-component/app-cash-model/app-cash-model.vue"

export default {
    data() {
        return {
            cashTypeModel: false,
            visible: false,
            mark_num: -1,
            community: {},
            pay_type: {
                auto: false,
                alipay: false,
                wechat: false,
                bank: false,
                balance: false,
            },
            loading: false,
            warningInfo: '',
            submitOver: false,
            config: [],
            name: '',
            bank_name: '',
            mobile: '',
            template_message: [],
            getMoney: null,
            moneyInput: '',
            cashType: '',
        }
    },
    components: {appCashModel,},
    computed: {
        ...mapState({
            mall: state => state.mallConfig.mall,
        }),
        ...mapGetters('mallConfig', {
            getTheme: 'getTheme',
        }),
        cashName() {
            switch (this.cashType) {
                case 'auto':

                    return '微信零钱';







                case 'wx':
                    return '微信线下打款';
                case 'alipay':
                    return '支付宝线下打款';
                case 'bank':
                    return '银联线下打款';
                case 'balance':
                    return '商城余额';
                default:
                    return;
                    break;
            }
        },
    },
    methods: {
        payTypeChange(value) {
            this.cashType = value;
            switch (value) {
                case 'auto':
                    this.mark_num = 0;
                    break;
                case 'wx':
                    this.mark_num = 1;
                    break;
                case 'alipay':
                    this.mark_num = 2;
                    break;
                case 'bank':
                    this.mark_num = 3;
                    break;
                case 'balance':
                    this.mark_num = 4;
                    break;
                default:
                    this.mark_num = -1;
                    break;
            }
        },
        subscribe() {
            if (this.loading) {
                return false
            }
            if (this.submitOver || this.warningInfo) {
                return false
            }
            if (this.moneyInput > 0) {
                if (this.mark_num == -1) {
                    uni.showToast({title: '请选择提现方式', icon: 'none'});
                    return;
                }
                this.loading = true;
                this.$subscribe(this.template_message).then(res => {
                    this.submit();
                }).catch(res => {
                    this.submit();
                });
            }else {
                this.warningInfo = '请输入正确的提现金额'
            }
        },
        submit() {
            let that = this;
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            let para = {
                model: 'community',
                price: that.moneyInput,
                type: 'auto'
            };
            if (that.mark_num == 4) {
                para.type = 'balance';
            } else if (that.mark_num == 0) {
                para.type = 'auto';
            } else {
                para.name = that.name;
                para.mobile = that.mobile;
                if (that.mark_num == 1) {
                    para.type = 'wechat';
                }
                if (that.mark_num == 2) {
                    para.type = 'alipay';
                }
                if (that.mark_num == 3) {
                    para.type = 'bank';
                    para.bank_name = that.bank_name;
                }
            }
            that.$request({
                url: that.$api.finance.cash,
                data: para,
                method: 'post'
            }).then(response=>{
                uni.hideLoading();
                that.loading = false;
                if(response.code == 0) {
                    that.submitOver = true;
                }else {
                    that.warningInfo = response.msg;
                }
            }).catch(response => {
                that.loading = false;
                uni.hideLoading();
            });
        },

        toDetail() {
            this.submitOver = false;
            uni.redirectTo({
                url: '/plugins/community/cash-detail/cash-detail'
            });
        },

        getStatus() {
            let that = this;
            that.$request({
                url: that.$api.community.index,
            }).then(response=>{
                that.$hideLoading();
                if(response.code == 0) {
                    that.community = response.data.middleman;
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
            });
        },

        setting() {
            let that = this;
            that.$request({
                url: that.$api.finance.config,
                data: {
                    model: 'community'
                },
                method: 'post'
            }).then(response=>{
                that.$hideLoading();
                that.getStatus();
                if(response.code == 0) {
                    that.config = response.data.config;
                    that.template_message = response.data.template_message;
                    for (let i = 0; i < that.config.pay_type.length; i++) {
                        switch (that.config.pay_type[i]) {
                            case "auto":
                                that.pay_type.auto = true;
                                //that.mark_num = 0;
                                break;
                            case "alipay":
                                that.pay_type.alipay = true;
                                //that.mark_num = 2;
                                break;
                            case "wechat":
                                that.pay_type.wechat = true;
                                //that.mark_num = 1;
                                break;
                            case "balance":
                                that.pay_type.balance = true;
                                //that.mark_num = 4;
                                break;
                            case "bank":
                                that.pay_type.bank = true;
                                //that.mark_num = 3;
                                break;
                        }
                    }
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
            });
        },
    },

    onLoad(options) { this.$commonLoad.onload(options);
        let that = this;
        if (options.money > 0) {
            that.money = options.money;
        };
        that.setting();
    }
}
